.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.logo-container img {
  height: 75px; /* Adjust based on your logo size */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .logo-container img {
      height: 50px; /* Smaller logo on mobile */
  }
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .navigation {
      display: none; /* Or switch to a hamburger menu */
  }
}

.rounded-btn {
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  border-radius: 20px; /* Rounded edges for buttons */
  text-decoration: none; /* Removes link underlining */
  color: inherit; /* Inherits the text color from the parent */
}

.login-link-button {
  display: flex;
  justify-content: flex-end;
}

.login-button {
  background-color: transparent;
  color: black;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .rounded-btn {
      margin: 0 5px; /* Smaller margins on mobile */
      padding: 5px 10px; /* Smaller padding on mobile */
  }

  .login-link-button {
      flex-direction: column; /* Stack elements vertically on small screens */
  }
}

/* Additional responsive adjustments for header */
@media (max-width: 768px) {
  .header {
      padding: 0 10px; /* Less padding on small screens */
  }

  .logo-container, .login-container {
      flex: 1;
  }
}

  
  