/* Support.css */
.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
 
  padding: 20px;
}

.flex-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
}

.support-section {
  text-align: center;
  margin-right: 20px;
}

.logo-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-section img {
  max-width: 100%;
  height: auto;
}

/* Styles supplémentaires pour le formulaire */
.support-form {
  width: 100%; /* Utilisez toute la largeur disponible pour le centrage */
}

.input-group {
  margin-bottom: 20px; /* Espace entre les champs de formulaire */
  text-align: center; /* Centre le contenu des groupes d'input */
}

.input-group label,
.input-group input,
.input-group textarea,
.support-button {
  width: 100%; /* Utilisez toute la largeur disponible */
  max-width: 300px; /* Ajustez selon la largeur maximale souhaitée pour les éléments */
  margin: auto; /* Centrage automatique des éléments */
}

.support-button {
  display: block; /* Assurez-vous que le bouton est un bloc pour le centrer */
}




