.Footer {
  margin-top: auto;
  position: absolute; /* Position fixe par rapport à la fenêtre du navigateur */
  bottom: 0; /* Aligner en bas de la fenêtre du navigateur */
  width: 100%; /* S'étend sur toute la largeur de la page */
  font-family: sans-serif;
  text-align: center;
  background-color: #333; /* Ajouté pour une meilleure visibilité */
  color: #fff; /* Ajouté pour une meilleure visibilité */
  padding: 0; /* Ajouté pour un peu d'espacement */

}

.Navfooter {
  display: grid;
  grid-area: Navfooter;
  grid-template-columns: repeat(4, auto);
  align-items: center;
  justify-items: center;
  /* Ajouter une marge en haut si nécessaire pour éviter le chevauchement avec le contenu de la page */
}

.Navfooter a {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  transition: 0.5s;
  text-decoration: none;
}

.Navfooter a:hover {
  transform: scale(1.1);
}

.Navfooter button {
  padding: 10px;
  outline: none;
  border: none;
  font-size: 10px;
  color: #fff;
  font-weight: 600;
  background-color: #f57328;
  box-shadow: 0px 5px 0px 0px rgba(255, 0, 0, 0.25);
  border-radius: 10px;
  cursor: pointer;
  transition: 70ms;
}

.Navfooter button:active {
  transform: translateY(3px);
  box-shadow: 0px 2px 0px 0px rgba(255, 0, 0, 0.25);
}

.bottom {
  margin-top: 30px;
  position: relative;
  bottom: 0;
  width: 100%;
  background: #411530;
  color: #ffffff;
  text-align: justify;
  padding: 10px 0;
  box-sizing: border-box;
  /* Ajoutez d'autres styles ici si nécessaire */
}

