.products-page {
  text-align: center;
  max-width: 1200px; /* ou toute autre largeur que vous préférez */
  margin: auto; /* Centrer la page */
}

.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* Espace entre les produits */
}

.product {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(50% - 10px); /* Ajuster en fonction de l'espace entre les produits */
  border: 1px solid #ddd; /* Pour visualiser les contours */
  padding: 10px;
  box-sizing: border-box;
}

.product-image img {
  width: 150px; /* Ajustez selon la taille souhaitée */
  height: auto;
  margin-right: 10px;
}

button {
  margin-top: 10px;
  padding: 10px;
  background-color: #f57328;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #e56222;
}

  