.orders-page {
  text-align: center;
  margin: auto;
}

.orders-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 75px; /* Espace entre les produits */
}

.order {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(50% - 10px); /* Ajuster en fonction de l'espace entre les produits */
  border: 1px solid #ddd; /* Pour visualiser les contours */
  padding: 10px;
  box-sizing: border-box;
}

.order-details {
  flex: 1; /* Pour que les détails prennent l'espace disponible */
}

.order-image {
  text-align: center; /* Centrer l'image et le bouton */
}

.order-image img {
  width: 100px; /* Ajustez selon la taille souhaitée */
  height: auto; /* Pour maintenir l'aspect ratio */
}

button {
  margin-top: 10px; /* Espace entre l'image et le bouton */
  padding: 10px 20px; /* Ajustez le padding selon vos besoins */
  /* Autres styles pour le bouton */
}


  